import React from 'react'
import { Switch, Route } from "react-router-dom";
import DataList from "./dataListComponent";
import Details from "./detailComponent";
import NoMatch from "./noMatchComponent";
import { useDataApi } from "../customHook/useDataApi";

export default function Home() {
    // custom hook
    const [state, setUrl] = useDataApi(
        'http://localhost:3001/movie/',
        [],
    )

    const handleChangeUrl = (url) => {
        setUrl(url)
    }
    
    return (
        <>  
            <Switch>
                <Route exact path={`/`}>
                    <DataList state={state} onChangeUrl={handleChangeUrl} />
                </Route>
                <Route exact path={`/movie/:id`}>
                    <Details />
                </Route>
                
                <Route path="/movie/*">
                    <NoMatch />
                </Route>
            </Switch>       
        </>
    )
}
