import React from "react";
import { CardImg } from "reactstrap";
import { useParams } from 'react-router-dom'
import { useDataApi } from "../customHook/useDataApi";

export default function Details(props) {
    const { id } = useParams()
    const [state] = useDataApi(
        `http://localhost:3001/movie/${id}`,
        [],
    )
    const data = state.data[0]
    
    if (state.error) {
        return(
            <div className="container p-3">
                <p className="text-danger">{state.error}</p>
            </div>
        ) 
    }else if (state.isLoading) {
        return <p>Loading ...</p>
    }else {
        return (
            <>
                { data && (
                    <div>
                        <h3 className="text-center">{data.title}</h3>
                        <div className="col-sm-10 col-md-6 m-auto">
                            <CardImg top width="100%" src={data.poster} alt="Card image cap" />
                            <p>{data.plot}</p>
                            <p>IMDb Rating {data.imdb.rating}</p>
                        </div>
                    </div>
                )}
            </>
        )
    }
    
}

