import React, { useState } from "react";
import { Card, CardImg, Spinner, Button, Form, FormGroup, CustomInput, Label, Input } from "reactstrap";
import { Link } from "react-router-dom";


export default function DataList({state, onChangeUrl}) {
    const filters = state.filters
    const initialSearchBy = Object.keys(filters)[0]
    let initialSearchText
    switch (initialSearchBy) {
        case "title":
            initialSearchText = filters[initialSearchBy]
            break;
        case "cast":
            initialSearchText = filters[initialSearchBy]
            break;
        case "year":
            initialSearchText = filters[initialSearchBy]
            break;
        default:
            break;
    }
    const [searchText, setSearchText] = useState(initialSearchText || '')
    const [searchBy, setSearchBy] = useState(initialSearchBy || 'title')
    const [active, setActive] = useState('');

    if (filters.languages) {
        var initailLanguages = filters.languages.split(',')
    }
    const [languages, setLanguages] = useState(initailLanguages || [])
    
    const baseUrl = 'http://localhost:3001/movie/'
    const [nextPageUrl, setNextPageUrl] = useState(baseUrl)
    let url = baseUrl
    
    const toggle = () => {
        if (!active) {
            return setActive('active')
        }
        setActive('')
    }
    
    const handleText = (e) => {
        setSearchText(e.target.value)
    }
    const handleChange = (e) => {
        setSearchBy(e.target.value)
        setSearchText('')
    }
    const handleSubmit = (e) => {
        if (searchText && languages.length > 0) {
            url = `${baseUrl}?${searchBy}=${searchText}&languages=${languages}`
        }else{
            url = `${baseUrl}?${searchBy}=${searchText}`
        }
        onChangeUrl(url)
        setNextPageUrl(url)
        e.preventDefault()
    }

    // sidebar filter
    const handleCheckBox = (e) => {
        const value = e.target.value
        if (languages.includes(value)) {
            setLanguages(languages.filter(item => item !== value))
        }else {
            setLanguages(languages.concat(value))
        }
    }
    const handleCBSubmit = (e) => {
        if(searchText && languages.length > 0){
            url = `${baseUrl}?${searchBy}=${searchText}&languages=${languages}`
        }else if(languages.length > 0){
            url = `${baseUrl}?languages=${languages}`
        }else if (searchText && languages.length === 0) {
            url = `${baseUrl}?${searchBy}=${searchText}`
        }
        onChangeUrl(url)
        setNextPageUrl(url)
    }
    // handleClick function
    const handleClickNext = () => {
        if (nextPageUrl === baseUrl) {
            onChangeUrl(`${baseUrl}?page=${state.page + 1}`)
        }else{
            onChangeUrl(`${nextPageUrl}&page=${state.page + 1}`)
        }
    }
    const handleClickPrevious = () => {
        if (nextPageUrl === baseUrl) {
            onChangeUrl(`${baseUrl}?page=${state.page - 1}`)
        } else {
            onChangeUrl(`${nextPageUrl}&page=${state.page - 1}`)
        }
    }

    const items = (state.data.length > 0) ? (
            state.data.map((item, index) => <Post key={index} item={item} />)
        ) : (
            !state.isLoading && <p>No Result found!</p>
        )

    return (
        <div className="wrapper text-left">
            <div className={`sidebar ${active}`} >
                <div onClick={toggle} className="btn close-btn">&times;</div>
                
                <Form className="px-3" onSubmit={handleCBSubmit}>
                    <FormGroup>
                        <Label>Languages:</Label>
                        <div>
                            <CustomInput type="checkbox" id="cb" name="cb" label="English" value="English" 
                                onChange={handleCheckBox} checked={languages.includes('English')}/>
                            <CustomInput type="checkbox" id="cb2" name="cb" label="Hindi" value="Hindi" 
                                onChange={handleCheckBox} checked={languages.includes('Hindi')} />
                            <CustomInput type="checkbox" id="cb3" name="cb" label="Tamil" value="Tamil" 
                                onChange={handleCheckBox} checked={languages.includes('Tamil')} />
                        </div>
                    </FormGroup>  
                    <Button onClick={handleCBSubmit}>Apply</Button> 
                </Form>
            </div>
            <div className="content">
                <Button size="sm" onClick={toggle} className="filter">Filter</Button>

                <Form className="pt-2 text-left" onSubmit={handleSubmit}>
                    <Input type={ (searchBy === 'year') ? "number" : "text"} placeholder="Search..." name="searchText" value={searchText} onChange={handleText}/>
                    <FormGroup>
                        <Label for="radio">Search By</Label>
                        <div>
                            <CustomInput type="radio" id="radio" name="customRadio" label="Title" value="title" onChange={handleChange} 
                                checked={(searchBy === 'title') ? true : false} inline />
                            <CustomInput type="radio" id="radio2" name="customRadio" label="Cast" value="cast" onChange={handleChange}
                                checked={(searchBy === 'cast') ? true: false} inline />
                            <CustomInput type="radio" id="radio3" name="customRadio" label="Released Year" value="year" 
                                onChange={handleChange} checked={(searchBy === 'year') ? true : false} inline />

                        </div>
                    </FormGroup>
                    {/* <Button type="submit">Submit</Button> */}
                </Form>
                <p>
                    Toatal Results : {state.totalResults}
                </p>
                <div className="row px-2">
                    {(state.error) ?
                        <p className="text-danger m-auto">{state.error}</p>
                        : items}
                    {(state.isLoading) && <Spinner className="m-auto" type="grow" color="info" /> }
                </div>
                <Button className="mr-3" color="danger" onClick={handleClickPrevious}disabled={ state.page === 0 ? true : false}>
                    Previous
                </Button>
                <Button color="danger" onClick={handleClickNext}
                    disabled={(state.totalResults - ((state.page + 1) * state.moviesPerPage)) < state.moviesPerPage ? true : false}
                >
                    Next
                </Button>
            </div>
        </div>
    )
}


function Post({ item }) {
    return (
        <div className="col-6 col-sm-3 col-lg-2 p-2 fade-in">
            <Link to={`/movie/${item._id}`} className="link">
                <Card>
                    <CardImg top width="100%" height="208px" src={item.poster} alt="Card image cap" />
                    {/* <p className="text-center">{item.title}</p> */}
                </Card>
            </Link>
        </div>
    )
}


