const fetchDataReducer = (state, action) => {
    switch (action.type) {
        case 'FETCH_INIT':
            return {
                ...state,
                isLoading: true,
                error: ''
            }
        case 'FETCH_SUCCESS':
            return {
                ...state,
                isLoading: false,
                data: action.payload.data,
                filters: action.payload.filters,
                page: action.payload.page,
                moviesPerPage: action.payload.moviesPerPage,
                totalResults: action.payload.totalResults,
            }
        case 'FETCH_FAILURE':
            return {
                ...state,
                isLoading: false,
                error: action.payload
            }
        default:
            throw new Error()
    }
}
export default fetchDataReducer